import React from 'react';
import {ButtonGroup, BottomNavigationAction,BottomNavigation , Box, IconButton, Typography, Slider, Grid, AppBar, Button, Toolbar, Container } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import GitHubIcon from '@mui/icons-material/GitHub';
import DownloadIcon from '@mui/icons-material/Download';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import { styled } from '@mui/material/styles';
import {Howl, Howler} from "howler";
import boop from "../sounds/boop.mp3";
import algovid from "../videos/algodemo.mp4";
import Stack from '@mui/material/Stack';
import VolumeDown from '@mui/icons-material/VolumeDown';
import VolumeUp from '@mui/icons-material/VolumeUp';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import RestartAltIcon from '@mui/icons-material/RestartAlt';


const PRIMARY_COLOR = '#0091ea';
const SECONDARY_COLOR = '#81d4fa';

var ARRAY_LENGTH = 10;
var ARRAY_SPEED = 500;
var prev_speed = 0;
var prev_length = 10;
var myInterval;
var intervalFinished = true;
const sound = new Howl({
  src: boop
})
Howler.volume(0)

const StyledToolbar = styled(Toolbar)(({ theme }) => ({
  alignItems: 'flex-start',
  paddingTop: theme.spacing(1),
  paddingBottom: theme.spacing(1),
  // Override media queries injected by theme.mixins.toolbar
  '@media all': {
    minHeight: 32,
  },
}));


export default class SortingVisualizer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      array: [],
    };
  }
  valuetext(value) {
    if (prev_speed !== value) {
      if (value === 100) {
        ARRAY_SPEED = 35;
      } else if (value === 75) {
        ARRAY_SPEED = 150;
      } else if (value === 50) {
        ARRAY_SPEED = 300;
      } else {
        ARRAY_SPEED = 750;
      }
    }
    // this.resetArray(value);
    prev_speed = value;

  }
  onDownload = () => {
    let a = document.createElement('a');
    a.download = `AlgoVisualDemo.mp4`;
    a.href = algovid;
    a.click();
  };
  
  valuetextArray(value) {
    if (prev_length !== value) {
      ARRAY_LENGTH = value;

    }

  }

  componentDidMount() {
    this.resetArray(ARRAY_LENGTH);
  }


  resetArray() {
    if (intervalFinished === true) {

      const array = [];
      for (let i = 0; i < 10-1; i++) {
        array.push(randomIntFromInterval(5, 100));
      }
      array.push(100)
      this.setState({array});
    }
  }

  handleChange = (event) => {
    ARRAY_LENGTH = event.target.value;
  };
  
  handleSound = (event, newValue) =>  {
    //
    Howler.volume(newValue/100);
  };
  
  generateArray() {
    const array = [];
    for (let i = 0; i < 10-1; i++) {
      array.push(randomIntFromInterval(5, 100));
    }
    array.push(100)
    this.setState({array});
  }

  updateBarColors(animations_i, animations_j, heights_i, heights_j) {
    clearInterval(myInterval);
    intervalFinished = false;
    var i = 0;
    var j = 0;
    var arrayBars = document.getElementsByClassName('array-bar');
    myInterval = setInterval(() => {
      if (i !== 0){
        arrayBars[animations_i[i-1]].style.backgroundColor = PRIMARY_COLOR;
        arrayBars[animations_j[j-1]].style.backgroundColor = PRIMARY_COLOR;
      }
      arrayBars[animations_i[i]].style.backgroundColor = SECONDARY_COLOR;
      arrayBars[animations_i[i]].style.height = `${heights_i[i]*7}px`;
      arrayBars[animations_i[i]].textContent = heights_i[i];
      arrayBars[animations_j[j]].style.backgroundColor = SECONDARY_COLOR;
      arrayBars[animations_j[j]].style.height = `${heights_j[j]*7}px`;
      arrayBars[animations_j[j]].textContent = heights_j[j];
      i += 1;
      j += 1;
      sound.play();
    }, ARRAY_SPEED);
    setTimeout(() => {
      clearInterval(myInterval);
      intervalFinished = true;
    }, ARRAY_SPEED * animations_i.length + 2*ARRAY_SPEED);
  }

  bubbleSort() {
    if (intervalFinished === true) {
      var arr = this.state.array;
      var animations_i = [];
      var animations_j = [];
      var heights_i = [];
      var heights_j = [];
      for (let i=0; i < arr.length; i++) {
        for(let j = 0; j < ( arr.length - i -1 ); j++){
          animations_i.push(j);
          animations_j.push(j+1);
          // Checking if the item at present iteration 
          // is greater than the next iteration
          if(arr[j] > arr[j+1]){
            // If the condition is true then swap them
            var temp = arr[j]
            arr[j] = arr[j + 1]
            arr[j+1] = temp
          }
          heights_i.push(arr[j]);
          heights_j.push(arr[j+1]);
        }
      }
      this.updateBarColors(animations_i, animations_j, heights_i, heights_j);
    }   
  } 

  render() {
    const {array} = this.state;

    return (
      <div className="App">
        <Box sx={{ flexGrow: 1 }}>
        <AppBar position='static' style={{ background: '#2196f3'}}>
        <StyledToolbar sx={{ flexGrow: 1 }}>
        <Grid
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center"
            columns={12}
            spacing={1}
          >


          <Grid item xs={2.85} >
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>


          </Grid>
                      <Grid item xs={3}>
<ButtonGroup variant="contained" aria-label="outlined primary button group">
      <Button style={{textTransform: 'none',color: 'white', fontSize: 18,}} onClick={() => this.bubbleSort()} variant="contained">Quick Sort {<PlayArrowIcon />}</Button>
      <Button style={{textTransform: 'none',color: 'white', fontSize: 18,}} onClick={() => this.bubbleSort()} variant="contained">Bubble Sort {<PlayArrowIcon />}</Button>
      <Button style={{textTransform: 'none',color: 'white', fontSize: 18,}} onClick={() => this.bubbleSort()} variant="contained">Quick Sort {<PlayArrowIcon />}</Button>
    </ButtonGroup>




              
            </Grid>


            <Grid item xs={1.5}>
            <Button style={{textTransform: 'none',color: 'white', fontSize: 18,}} onClick={() => this.resetArray()} variant="contained">
                Create New Array{<RestartAltIcon />}
              </Button>
            </Grid>


            <Grid item xs={1.5}>
            <Stack spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">
        <VolumeDown />
        <Slider aria-label="Volume" onChange={this.handleSound} />
        <VolumeUp />
      </Stack>
            </Grid>


          </Grid>
        </StyledToolbar>
      </AppBar>

        </Box>
        <Container maxWidth="xs">
          <Typography variant="h6" align='center'>Animation Speed</Typography>
        <Slider
        
          aria-label="Temperature"
          defaultValue={50}
          valueLabelDisplay="auto"
          getAriaValueText={this.valuetext}
          step={25}
          marks
          min={25}
          max={100}
        />
      </Container>
      <Container maxWidth="md">
        <Grid container spacing={2} columns= {ARRAY_LENGTH} direction="row" justifyContent="center" alignItems="flex-end">
          {array.map((value, idx) => (
            <Grid item xs={1}>
            <div 
              className='array-bar'
              key={idx}
              style={{
              backgroundColor: PRIMARY_COLOR,
              height: `${value*7}px`,
              textAlign: 'center',
              color: 'Black',
              fontWeight: 'bold',
            }} 
            >{value}</div>
            </Grid>
          ))}
        </Grid>
        <BottomNavigation
        showLabels
      > 
        <BottomNavigationAction href='https://en.wikipedia.org/wiki/Sorting_algorithm' label="Information" icon={<HelpOutlineIcon />} />
        <BottomNavigationAction href='https://github.com/HighlandsHercus/' label="GitHub" icon={<GitHubIcon />} />
        <BottomNavigationAction onClick={this.onDownload} label="Download" icon={<DownloadIcon />} />
      </BottomNavigation>
      </Container>
      </div>
    );
  }
}

function randomIntFromInterval(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

